@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Russo+One&display=swap');
body{
    background: white;
    color : black;
    font-family: Poppins , "Russo One";
}



.mytodos .title{
    text-align: center;
    word-spacing: 10px;
    letter-spacing: 2px;
    font-size : 30px;
}

.todoform , .todolist{
    display: flex;
    justify-content: center;
    align-items: center;
}

.todoform .input{
    margin : 20px auto;
    padding : 5px 15px;
    font-family: Poppins;
    width : 40%;
    height : 35px;
    outline : none;
    border : none;
    border-radius: 5px;
    border : 1px solid grey;
    font-size: 16px;
}



.todolist ul{ 
    width : 41.5%;
    padding: 0px;
    margin : 0px;
    box-shadow: 1px 1px 10px grey;
    border-radius: 5px;
    
}
.todolist li{
    
    padding : 5px 15px;
    color: black;
    font-size : 20px;
    font-family : Poppins;
    list-style: none;
    border-bottom : 2px solid orange
}